export async function getProducts(guid, limit = 10, pageNumber = 1, query = "", sort = "") {
    try {
        const response = await axios.get(apiUrlJs + "/offers/all/" + guid + "?items=" + limit + "&page=" + pageNumber + query + sort, {
            headers: {
                "x-api-key": awsKey,
            },
        });

        const prodList = [];
        response.data.offers.map((item, key) => {
            prodList.push(new Product(item));
        });
        return prodList;
    } catch (error) {
        console.log(error);
        return null;
    }
}

