export async function whoami(guid) {
    try {
        if (guid == null) {
            console.log("No GUID provided to 'whoami'");
            return null;
        }

        const response = await axios.get(apiUrlPy + "/whoami?guid=" + guid, {
            headers: { "x-api-key": awsKey },
        });

        let userJson = response.data[0];

        return new User(userJson);
    } catch (error) {
        console.log(error);
        return null;
    }
}

export async function updateThreshold(offerId, enabled, minPrice, maxPrice) {
    try {
        if (offerId == null) return;
        if (enabled == null) return;

        let body = {
            offerId: offerId,
            enabled: enabled,
            minPrice: minPrice,
            maxPrice: maxPrice,
        };

        const response = await axios.post(apiUrlPy + "/offers/thresholds/update", JSON.stringify(body), {
            headers: { "x-api-key": awsKey },
        });

        console.log(response);
        return "Success";
    } catch (error) {
        console.log(error);
        return null;
    }
}
