import React, { Component, useState, useEffect } from "react";
import "@trendmicro/react-sidenav/dist/react-sidenav.css";
import "bootstrap/dist/css/bootstrap.min.css";
import "../../App.css";
import { Tab, Tabs, TabList, TabPanel } from "react-tabs";
import "react-tabs/style/react-tabs.css";
import axios from "axios";
import { Product } from "../../models/ProductObject";
import api, { updateThreshold, getSingleOffer } from "../../helpers/api";
import $ from "jquery";
import { connect, useDispatch } from "react-redux";
import { update as updateProduct } from "../../state/actions";

// class ProductDetailsModal extends Component {
const ProductDetailsModal = (props) => {
    const [product, setProduct] = useState(props.offer);
    const [loading, setLoading] = useState(true);
    const [infoMessage, setInfoMessage] = useState(null);
    const [processing, setProcessing] = useState(false);
    const [defaultTab, setDefaultTab] = useState(props.defaultTab);

    const dispatch = useDispatch();

    useEffect(() => {
        setProduct(props.offer);
        fetchProduct(props.offer);
        setDefaultTab(props.options.defaultTab);
    }, [props.offer, props.options.defaultTab]);

    const updateProductRepository = () => {
        showMessage("Fetching latest data...");
        getSingleOffer(product.offerId).then((stateProduct) => {
            showMessage("Up to date", "success", true);
            setProcessing(false);
            if (stateProduct == null) return;
            dispatch(updateProduct(stateProduct));
        });
    };

    const fetchProduct = (offer) => {
        if (offer == null) return;
        if (offer.status != "Active") setLoading(false);

        let apiUrl = "https://yr2yl0lpui.execute-api.us-east-1.amazonaws.com/dev";
        axios
            .get(apiUrl + "/offers/" + offer.offerId, {
                headers: {
                    "x-api-key": "A8x8nBruJt5Obic2pzNdF3AtEzL4QOea6hN13D8A",
                },
            })
            .then(function (response) {
                let stateProduct = new Product(props.offer);
                let takealotProduct = new Product(response.data);

                stateProduct.merge(takealotProduct);

                setProduct(stateProduct);
                setLoading(false);
            })
            .catch(function (error) {
                console.log(error);
                showMessage(error.message, "danger");
                setLoading(false);
            });
    };

    const startLoading = () => {
        this.setState({ processing: true });
    };

    const stopLoading = () => {
        this.setState({ processing: false });
    };

    const showMessage = (message, type = "success", autoDismiss = false) => {
        setInfoMessage({
            message: message,
            type: type,
        });

        if (!autoDismiss) return;
        setTimeout(function () {
            setInfoMessage(null);
        }, 5000);
    };

    const getInfoMessage = () => {
        let message = infoMessage.message;
        let colourStyle = infoMessage.type;
        let showLoading = infoMessage.loading || false;

        return <div className={"col-8 text-" + colourStyle}>{message}</div>;
    };

    const validate = () => {
        let min = $("#minPrice").val();
        let max = $("#preferredPrice").val();

        if (parseInt(min) < 1) {
            showMessage("Your minimum price is less than R1. You're not giving it away for free are you?", "danger");
            return false;
        } else if (parseInt(min) > parseInt(max)) {
            showMessage("Your minimum price can't be more than your preferred price", "danger");
            return false;
        }

        if (parseInt(max) < 1) {
            showMessage("Your preferred price is less than R1. You're not giving it away for free are you?", "danger");
            return false;
        } else if (parseInt(min) > parseInt(max)) {
            showMessage("Your preferred price can not be less than your minimum price", "danger");
            return false;
        }

        return true;
    };

    const updatePrice = () => {
        if (!validate()) return;
        showMessage("Saving...");
        setProcessing(true);

        let min = $("#minPrice").val();
        let max = $("#preferredPrice").val();
        let enabled = $("#" + product.offerId + "_autoPricingSwitch")[0].checked || false;

        updateThreshold(product.offerId, enabled, parseFloat(min), parseFloat(max))
            .then(function (response) {
                console.log(response);
                showMessage("Saved!");
                updateProductRepository();
                // setProcessing(false);
            })
            .catch(function (error) {
                console.log(error);
                showMessage(error.message, "danger");
                setProcessing(false);
            });
    };

    const detailsTabPanel = () => {
        return (
            <TabPanel style={{ padding: "10px" }}>
                <div className="row" style={{ marginBottom: "30px" }}>
                    <div className="col-12">
                        <h5>Offer Details:</h5>
                    </div>
                </div>
                <div className="row">
                    <div className="col-4">
                        <img
                            src={product.image}
                            className="img-fluid"
                            style={{
                                width: "400px",
                                marginBottom: "10px",
                                border: "1px solid #d2d1d1",
                            }}
                        />
                        <h6>
                            <b>Price: R {product.currentPrice}.00</b>
                        </h6>
                        <a className="btn btn-success" style={{ color: "#fff", marginTop: "10px" }} href={product.url} target="_blank">
                            View Offer on Takealot
                        </a>
                    </div>
                    <div className="col-8">
                        <h5>
                            <b>{product.title}</b>
                        </h5>
                        <br />
                        <small>
                            <b>Brand: {product.brand}</b>
                        </small>
                        <br />
                        <br />
                        <h6>
                            <b>Description:</b>
                        </h6>
                        <br />
                        <div className="post__content" dangerouslySetInnerHTML={{ __html: product.description }}></div>
                    </div>
                </div>
            </TabPanel>
        );
    };

    const settingsTabPanel = () => {
        return (
            <TabPanel style={{ padding: "10px" }}>
                <div className="row" style={{ marginBottom: "30px" }}>
                    <div className="col-12">
                        <h5>Offer Settings:</h5>
                    </div>
                </div>
                <div className="row">
                    <div className="col-8">
                        <div className="row justify-content-center">
                            <div className="col-8">
                                <label className="form-label">Minimum price</label>
                                <input id="minPrice" type="number" className="form-control" defaultValue={product.minPrice} />
                                <small style={{ color: "#ff0000" }} id="minError"></small>
                            </div>
                        </div>
                        <div className="row justify-content-center">
                            <div className="col-8">
                                <label className="form-label">Preferred sale Price</label>
                                <input id="preferredPrice" type="number" className="form-control" defaultValue={product.preferredSalePrice} />
                                <small style={{ color: "#ff0000" }} id="maxError"></small>
                            </div>
                        </div>
                        <br></br>
                        <div className="row justify-content-center">
                            <div className="col-8">
                                <div class="custom-control custom-switch">
                                    <input
                                        type="checkbox"
                                        class="custom-control-input"
                                        name={product.offerId + "_autoPricingSwitch"}
                                        id={product.offerId + "_autoPricingSwitch"}
                                        defaultChecked={product.autoPricingEnabled}
                                    />
                                    <label class="custom-control-label" for={product.offerId + "_autoPricingSwitch"}>
                                        Enable the auto-pricing system?<br></br>This means we will <b>automatically</b> lower your sale price to beat
                                        other sellers.<br></br>
                                        <b>It will never go below your minimum or above your maximum.</b>
                                    </label>
                                </div>
                            </div>
                        </div>

                        <div className="row justify-content-center">
                            <div className="col-8">
                                <a className="btn btn-success" style={{ color: "#fff", marginTop: "30px" }} onClick={() => updatePrice()}>
                                    Save
                                </a>
                            </div>
                        </div>
                    </div>
                </div>
            </TabPanel>
        );
    };

    return (
        <div>
            <div
                class="modal fade"
                id="productDetailsModal"
                tabindex="-1"
                role="dialog"
                aria-labelledby="productDetailsModalLabel"
                aria-hidden="true"
            >
                <div class="modal-dialog modal-xl" role="document">
                    <div class="modal-content">
                        <div class="modal-header">
                            <h5 class="modal-title" id="productDetailsModalLabel">
                                Product details
                            </h5>
                            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                                <span aria-hidden="true">&times;</span>
                            </button>
                        </div>
                        <div class="modal-body">
                            <div className="col-12 text-left">
                                <div className="container py-4">
                                    <div className="row">
                                        <div className={loading || product == null ? "col-12 text-center" : "col-12"}>
                                            {loading || product == null ? (
                                                <i className="fa fa-spinner fa-spin fa-3x" style={{ marginTop: "100px" }}></i>
                                            ) : product.status == "Active" ? (
                                                <Tabs defaultIndex={defaultTab}>
                                                    <TabList>
                                                        <Tab>Details</Tab>
                                                        <Tab>Settings</Tab>
                                                    </TabList>
                                                    {detailsTabPanel()}
                                                    {settingsTabPanel()}
                                                </Tabs>
                                            ) : (
                                                <h3>This offer is inactive.</h3>
                                            )}
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        {infoMessage != null ? (
                            <div className="modal-footer" style={{ display: "block" }}>
                                <div className="row justify-content-center">{getInfoMessage()}</div>

                                {processing ? <i className="fa fa-spinner fa-spin" style={{ marginLeft: "5px" }}></i> : null}
                            </div>
                        ) : null}
                    </div>
                </div>
            </div>
        </div>
    );
};
export default ProductDetailsModal;
