import React, { useState, useEffect, Component } from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import { connect, useDispatch } from "react-redux";
import { MDBDataTable } from "mdbreact";
import { Product } from "../../models/ProductObject";
import ProductDetailsModal from "../modals/ProductDetailsModal";
import $ from "jquery";

let mapStateToProps = (state) => {
    return {
        guid: state.auth.user.guid,
        products: state.products.map((product) => {
            return Object.assign({}, product);
        }),
    };
};

class ProductsTable extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: false,
            selectedOffer: null,
            filter: props.filter,
            productModalOptions: {},
        };
    }

    didSelectRow(product, tabToOpen = 0) {
        console.log(product);
        this.setState({
            selectedOffer: product,
            productModalOptions: {
                defaultTab: tabToOpen,
            },
        });
        $("#productDetailsModal").modal("show");
    }

    render() {
        return this.props.products == null ? (
            <h1>No dice</h1>
        ) : (
            <div>
                <ProductDetailsModal offer={this.state.selectedOffer} options={this.state.productModalOptions} />
                <MDBDataTable
                    id="tablee"
                    name="tablee"
                    entries={20}
                    striped
                    bordered
                    hover
                    data={{
                        columns: [
                            { label: "Title", field: "title", sort: "asc", fixed: true },
                            { label: "Barcode", field: "barCode" },
                            { label: "GTIN", field: "gtin" },
                            { label: "Minimum price", field: "minPriceControl" },
                            { label: "Preferred price", field: "preferredPriceControl" },
                            { label: "Current price", field: "currentPrice" },
                            { label: "Status", field: "statusControl" },
                            { label: "Auto pricing", field: "autoPricingEnabledControl" },
                            { label: "Takelot link", field: "urlControl" },
                            { label: "Settings", field: "settings" },
                        ],

                        rows: this.props.products
                            .map((product) => {
                                product.autoPricingEnabledControl = (
                                    <div class="custom-control custom-switch">
                                        <input
                                            type="checkbox"
                                            class="custom-control-input"
                                            name={product.offerId + "_autoPricingSwitch"}
                                            id={product.offerId + "_autoPricingSwitch"}
                                            checked={product.autoPricingEnabled}
                                            disabled={true}
                                            onChange={() => {
                                                new Product(product).toggleAutoPricing();
                                            }}
                                        />
                                        <label class="custom-control-label" for={product.offerId + "_autoPricingSwitch"}></label>
                                    </div>
                                );

                                product.minPriceControl = (
                                    <button onClick={() => this.didSelectRow(product, 1)}>{product.minPrice || "  .  "}</button>
                                );
                                product.preferredPriceControl = (
                                    <button onClick={() => this.didSelectRow(product, 1)}>{product.preferredSalePrice || "  .  "}</button>
                                );

                                product.statusControl = (
                                    <span
                                        style={
                                            product.status == "Active" ? (product.isWinningBuybox ? { color: "#009900" } : { color: "#ff0000" }) : {}
                                        }
                                    >
                                        {product.status == "Active" ? (product.isWinningBuybox ? "Winning" : "Losing") : "Disabled"}
                                    </span>
                                );

                                product.urlControl = (
                                    <a href={product.url} target="_blank">
                                        View Offer
                                    </a>
                                );

                                product.settings = (
                                    <button onClick={() => this.didSelectRow(product)}>
                                        <span style={{ fontSize: 20, color: "grey" }}>
                                            <i class="fas fa-sliders-h"></i>
                                        </span>
                                    </button>
                                );

                                return product;
                            })
                            .filter((product) => {
                                switch (this.props.filter) {
                                    case "winning":
                                        return product.status == "Active" && product.isWinningBuybox;
                                    case "losing":
                                        return product.status == "Active" && !product.isWinningBuybox;
                                    case "active":
                                        return product.status == "Active";
                                    case "deactive":
                                        return product.status != "Active";
                                    default:
                                        return true;
                                }
                            }),
                    }}
                    style={{ zIndex: 999999 }}
                />
            </div>
        );
    }
}

export default connect(mapStateToProps)(ProductsTable);
