import * as constants from "../constants/types";
import { combineReducers } from "redux";

// Initialise state
export const initialState = {
    authenticated: false,
    guid: "",
    user: null,
    pageNumber: 1,
    registered: 0,
    showModal: 0,
};

const authReducer = (state = {}, action) => {
    switch (action.type) {
        case constants.LOGIN:
            return Object.assign({}, state, {
                user: action.payload,
            });

        case constants.LOGOUT:
            return {};

        case constants.IS_AUTHENTICATED:
            return state.user != null;

        default:
            return state;
    }
};

const defaultReducer = (state = initialState, action) => {
    if (action.type === constants.STORE_AUTH) {
        return Object.assign({}, state, {
            authenticated: action.payload.authenticated,
            guid: action.payload.guid,
            user: action.payload.user,
        });
    }

    if (action.type === constants.STORE_PAGE_NUMBER) {
        return Object.assign({}, state, {
            pageNumber: action.payload,
        });
    }

    if (action.type === constants.SET_REGISTERED) {
        return Object.assign({}, state, {
            registered: action.payload.registered,
            showModal: action.payload.showModal,
        });
    }

    return state;
};

const productsReducter = (state = [], action) => {
    switch (action.type) {
        case constants.UPDATE:
            // let product = state.find((prod) => prod.offerId == action.payload.offerId);
            let index = state.findIndex((prod) => prod.offerId === action.payload.offerId);
            if (index == -1) return state;

            let newState = Object.assign([], state);
            newState.splice(index, 1, action.payload);
            // state[index] = action.payload;
            // debugger;
            return newState;
        case constants.SET:
            return action.payload || [];
        case constants.CLEAR:
            return [];

        default:
            return state;
    }
};

const rootReducer = combineReducers({
    auth: authReducer,
    global: defaultReducer,
    products: productsReducter,
});

export default rootReducer;
