import {
    STORE_AUTH,
    STORE_PAGE_NUMBER,
    SET_REGISTERED,
    LOGIN,
    LOGOUT,
    IS_AUTHENTICATED,
    GET_ALL,
    GET,
    SET,
    CLEAR,
    UPDATE,
} from "../constants/types.js";

export const storeAuth = (payload) => {
    return {
        type: STORE_AUTH,
        payload,
    };
};

export const storePageNumber = (payload) => {
    return {
        type: STORE_PAGE_NUMBER,
        payload,
    };
};

export const setRegistered = (payload) => {
    return {
        type: SET_REGISTERED,
        payload,
    };
};

// AUTH

export const login = (payload) => {
    return {
        type: LOGIN,
        payload,
    };
};

export const logout = () => {
    return {
        type: LOGOUT,
    };
};

export const isAuthenticated = () => {
    return {
        type: IS_AUTHENTICATED,
    };
};

